<template>
   <div class="header">
    <ul class="header-button-left header-button" >
      <li v-if="step == 0" @click="goToStepThree">Followers</li>
      <li v-if="step === 1 || step === 2 || step === 3" @click="goToStepZero">Cancel</li>
    </ul>
    <ul class="header-button-right header-button">
      <li v-if="step == 1" @click="step++">Next</li>
      <li v-if="step == 2" @click="publish">Post</li>
      
    </ul>
    <!-- <img src="./assets/logo.png" class="logo" /> -->
  </div>
 
  <Container @write="postwrite = $event" :image="image" :post="post" :step="step"/>

  <!-- <button @click="more" class="more-button">More</button> -->

  <div class="footer">
    <ul class="footer-button-plus">
      <input @change="upload" type="file" id="file" class="inputfile" />
      <label for="file" class="input-plus">+</label>
    </ul>
  </div>

</template>

<script>
import Container from './components/ContainerView.vue';
import postdata from './assets/postdata.js';
import axios from 'axios';
axios.post()

export default {
  name: 'App',
  data() {
    return {
      step : 0,
      post : postdata,
      add : 0,
      image : '',
      postwrite : '',
      chooseFilter : ''
    }
  },
  mounted(){
    this.emitter.on('filterSelected', (a) => {
      this.chooseFilter = a
    })
  },
  components: {
     Container,
  },
  methods : {
    publish() {
      var myPost = {
        name: "Binn",
        userImage: "https://picsum.photos/100?random=3",
        postImage: this.image,
        likes: 36,
        date: "May 15",
        liked: false,
        content: this.postwrite,
        filter: this.chooseFilter
      };
      this.post.unshift(myPost);
      this.step = 0;
    },
    more() {
      axios.get(`https://codingapple1.github.io/vue/more${this.add}.json`)
      .then( result => {
        this.post.push(result.data);
        this.add++;
      })
    },
    upload(e){
      let f = e.target.files;
      let url = URL.createObjectURL(f[0]);
      console.log(url); 
      this.image = url;
      this.step++;
    },
    goToStepThree() {
    this.step = 3;
    },
    goToStepZero() {
    this.step = 0;
    }  
  },
}
</script>

