import { createStore } from 'vuex';

const store = createStore({
  state() {
    return {
      likes: [30, 50, 60],  
      likesPressed: [false, false, false],  
 
    };
  },
  mutations: {
    likes(state, i) {
      if (!state.likesPressed[i]) {
        state.likes[i]++;
        state.likesPressed[i] = true;
      } else {
        state.likes[i]--;
        state.likesPressed[i] = false;
      }
    },
  },
 
});

export default store;